@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
.App {
  font-family: "Open Sans", serif;
  text-align: center;
  font-size: 68px;
  font-weight: 300;
  color: white;
  margin-top: 100px;
}

.Title {
  text-align: center;
  font-size: 50px;
  font-weight: 100;
  color: white;
  margin-top: 100px;
}

.location {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 45px;
  margin-bottom: 15%;
  background-color: rgba(255, 255, 255, 0.1);
  border: 3px solid white;
  font-size: 40px;
}

input[type="text"] {
  font-family: "Open Sans", serif;
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f16051;
  width: 30%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

input[type="text"] { 
    outline: none;
}

/* input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
    -webkit-text-fill-color: #fff !important;
} */

button {
  font-family: "Open Sans", serif;
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100;
}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 300px;
  height: 300px;
}

button:active {
  outline: none;
}
.Input {
    text-align: center;
    background-color: transparent;
  }
  
  .textInput {
    text-align: center;
    background-color: rgba(255, 255, 255, 0.1);
    border : solid 1px white;
    border-radius: 20px;
    font-family: 'Baloo 2', cursive;
    outline: none;
    font-size: 24px;
    color: white;
  }
  
  ::-webkit-input-placeholder {
    color: white;
    opacity: 1; /* Firefox */
  }
  
  :-ms-input-placeholder {
    color: white;
    opacity: 1; /* Firefox */
  }
  
  ::-ms-input-placeholder {
    color: white;
    opacity: 1; /* Firefox */
  }
  
  ::placeholder {
    color: white;
    opacity: 1; /* Firefox */
  }
  
  .textInput:hover,
  .textInput:active,
  .textInput:focus{
    border: solid 2px white;
  }
html {
    height: 100%;
  }
  
body {
    font-family: "Open Sans", serif;;
    height: 100%;
    margin: 0;
    padding: 0;
    background-image: linear-gradient(#97a6b4 0%, #426ca0 100%);
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
}
